.formatted-content
    table
        border-spacing: 0
        // table-layout: fixed
        width: 100%
        margin: 1rem 0
        overflow: hidden
        overflow-x: auto

        tr:first-child
            > td:first-child,
            > th:first-child
                border-top-left-radius: var(--radius-m)

            > td:last-child,
            > th:last-child
                border-top-right-radius: var(--radius-m)

        tr:last-child
            td, th
                border-bottom: 1px solid var(--color-border)

            > td:first-child,
            > th:first-child
                border-bottom-left-radius: var(--radius-m)

            > td:last-child,
            > th:last-child
                border-bottom-right-radius: var(--radius-m)

        td,
        th
            min-width: 1rem
            border-top: 1px solid var(--color-border)
            border-left: 1px solid var(--color-border)
            padding: .5rem
            vertical-align: top
            box-sizing: border-box
            position: relative

            &:last-child
                border-right: 1px solid var(--color-border)

            > *
                margin-bottom: 0

            > *:last-child
                margin-bottom: 0

            > *:first-child
                margin-top: 0

        th
            font-weight: bold
            text-align: left
            background-color: #f1f3f5

        .selectedCell::after
            z-index: 2
            position: absolute
            content: ""
            left: 0
            right: 0
            top: 0
            bottom: 0
            background: rgba(200, 200, 255, 0.4)
            pointer-events: none

        .column-resize-handle
            position: absolute
            right: -2px
            top: 0
            bottom: -2px
            width: 4px
            background-color: #adf
            pointer-events: none

        p
            margin: 0

    .tableWrapper
        padding: 1rem 0
        overflow-x: auto

    .resize-cursor
        cursor: ew-resize
        cursor: col-resize

    img.content-image
        object-fit: cover
        object-position: center
        width: 100%
        max-width: 100%
        border-radius: var(--radius-m)
        background: var(--color-background-soft)
        display: block
        margin-block: 1em
        box-sizing: border-box

        &.aspect-16-by-9
            aspect-ratio: 16/9

        &.aspect-4-by-3
            aspect-ratio: 4/3

        &.aspect-1-by-1
            aspect-ratio: 1/1

        &.ProseMirror-selectednode
            box-shadow: 0 0 0 2px var(--color-info)

    a
        color: var(--color-primary)
        text-decoration: none

        &:hover
            text-decoration: underline
            color: var(--color-primary-soft)

    b
        font-weight: 600

    p
        margin-block: 1em
        padding-block: 1px

    li > p
        margin-block: 0

    .key-fact
        border-radius: var(--radius-m)
        background: var(--color-background-soft)
        padding: 1em
        margin: 1em 0
        display: flex
        gap: 1em
        align-items: center

        .icon
            font-size: 2em
            line-height: 1
            display: flex
            align-items: center
            justify-content: center
            aspect-ratio: 1
            color: var(--color-primary)
            font-family: var(--font-icon)
            user-select: none !important

            &::after
                content: attr(data-icon)

        .content
            *:first-child
                margin-top: 0
            
            *:last-child
                margin-bottom: 0

    blockquote.content-blockquote
        background: var(--color-background-soft)
        border-radius: var(--radius-m)
        padding: 2em 1em 1px 1em
        margin: 1rem 0
        position: relative

        &::before
            content: 'format_quote'
            position: absolute
            top: 5px
            left: 5px
            color: var(--color-text)
            font-family: var(--font-icon)
            font-size: 3em
            user-select: none
            opacity: .2
            pointer-events: none
            line-height: 1